import React, { useState } from 'react';

const CourseTop = () => {
    const [isExpanded, setIsExpanded] = useState(false);

    const toggleExpansion = () => {
        setIsExpanded(!isExpanded);
    };

    return (
        <div className="flex flex-col text-grey justify-center">
            <h2 className="text-2xl font-bold mb-4 leading-tight ">
                What is Python Developer Training?
            </h2>
            <div
                className="text-lg overflow-hidden transition-all duration-700"
                style={{ maxHeight: isExpanded ? 'none' : '80px' }}
            >
                <p className="text-justify">
                    Python Developer training is designed to provide learners with comprehensive skills in Python programming, suitable for various software development applications. Python is a versatile programming language widely used in enterprise applications, web development, mobile applications, and more..
                </p>
                <p className="text-justify mt-4">
                    This course covers fundamental Python programming concepts, data structures, object-oriented programming (OOP), web development with frameworks like Django or Flask, data analysis and visualization, and deployment strategies.
                </p>
                <p className="text-justify mt-4">
                    Students will learn to build web applications, automate tasks, work with databases, and integrate Python into various domains such as machine learning, web scraping, and scientific computing.
                </p>
                <p className="text-justify mt-4">
                    By the end of the course, learners will have a strong foundation in Python programming and practical experience in developing applications, preparing them for roles as Python developers, data analysts, automation engineers, and more.
                </p>
            </div>
            <button
                className="underline text-left mt-3 focus:outline-none hover:text-orange self-center"
                onClick={toggleExpansion}
            >
                {isExpanded ? 'Show less' : 'Show more'}
            </button>
        </div>
    );
};

export default CourseTop;
