import React, { useState } from 'react';
import { FaChevronDown, FaChevronUp } from 'react-icons/fa'; // Ensure you have react-icons installed

const FAQ = () => {
  const [openFAQ, setOpenFAQ] = useState(null);

  const toggleFAQ = (index) => {
    setOpenFAQ(openFAQ === index ? null : index);
  };

  const faqs = [
    {
      question: "What is Python Programming?",
      answer: "Python is a high-level, interpreted programming language known for its simplicity and readability. It is widely used in web development, data analysis, artificial intelligence, scientific computing, and more.",
    },
    {
      question: "What are the essential skills for a Python Developer?",
      answer: "Essential skills for a Python Developer include proficiency in Python programming, understanding of data structures and algorithms, knowledge of web frameworks like Django or Flask, experience with database management systems (SQL and NoSQL), and familiarity with tools for data analysis and machine learning.",
    },
    {
      question: "Why should I learn Python?",
      answer: "Python is popular due to its versatility, ease of learning, and a vast ecosystem of libraries and frameworks. It is used in various fields such as web development, data science, automation, artificial intelligence, and more, making it a valuable skill for developers.",
    },
    {
      question: "What are the advantages of using Django for web development?",
      answer: "Django is a high-level Python web framework that promotes rapid development and clean, pragmatic design. It includes built-in features like an ORM, admin interface, and authentication system, making it ideal for building robust and scalable web applications.",
    },
    {
      question: "How does Python support data analysis and machine learning?",
      answer: "Python has libraries like NumPy, Pandas, and Matplotlib for data manipulation, analysis, and visualization. For machine learning, libraries like TensorFlow and PyTorch provide tools for building and training models, making Python a preferred language for data-driven applications.",
    },
    {
      question: "What are the career prospects for Python Developers?",
      answer: "Python Developers are in high demand across industries such as tech, finance, healthcare, and more. They can pursue roles such as Python Developer, Data Analyst, Machine Learning Engineer, DevOps Engineer, and Software Engineer, with opportunities for career growth and specialization.",
    },
    {
      question: "How can Python be used in automation?",
      answer: "Python's simplicity and readability make it suitable for automation tasks. Libraries like Selenium are used for web automation, while scripting capabilities enable automation of repetitive tasks in system administration, testing, and deployment processes.",
    },
    {
      question: "What are some popular Python IDEs?",
      answer: "Popular Python IDEs (Integrated Development Environments) include PyCharm, VS Code with Python extension, Spyder, and Jupyter Notebook. These IDEs provide features such as code completion, debugging, and project management to enhance productivity.",
    },
    {
      question: "How can Python be integrated with databases?",
      answer: "Python supports integration with both SQL (e.g., SQLite, PostgreSQL) and NoSQL (e.g., MongoDB) databases. Libraries like SQLAlchemy facilitate ORM (Object-Relational Mapping) for SQL databases, while MongoDB drivers enable interaction with NoSQL databases, providing flexibility in data storage and retrieval.",
    },
    {
      question: "What are some applications of Python in scientific computing?",
      answer: "Python is widely used in scientific computing for simulations, data analysis, and visualization. Libraries like SciPy and scikit-learn provide tools for scientific and statistical computations, while Matplotlib and Plotly enable visualization of data and results.",
    },
  ];

  return (
    <div className="container mx-auto mt-16">
      <h2 className="text-center text-xl md:text-3xl font-bold mb-10 leading-tight">
        Frequently Asked Questions about Python Programming
      </h2>
      <div className="flex flex-col gap-4">
        {faqs.map((faq, index) => (
          <section key={index} className="border-b pb-2">
            <div
              className="flex justify-between items-center cursor-pointer"
              onClick={() => toggleFAQ(index)}
            >
              <h3 className="font-semibold text-lg">{faq.question}</h3>
              {openFAQ === index ? <FaChevronUp /> : <FaChevronDown />}
            </div>
            {openFAQ === index && <p className="mt-2">{faq.answer}</p>}
          </section>
        ))}
      </div>
    </div>
  );
};

export default FAQ;
